<template>
  <div class="orderBox" v-if="message.length">
    <div class="orderList">
      <div class="orderItem" :class="{'last-item': !message[index+1]}" v-for="(item,index) in message">
        <div class="orderSn clearfix">
          <span class="sn fl">订单编号: {{item.sn}}</span>
          <span class="status fr" :class="'status'+item.status">{{status(item.status)}}</span>
        </div>
        <div class="orderMsg clearfix">
          <div class="btn-price fr">
            <div class="logo"><img :src="baseUrl + item.brand_info.logo"></div>
            <div class="price">
              <span class="p">{{item.amount}}</span>
              <span class="i">元</span>
            </div>
            <div class="btns">
               <div class="btn baodan" v-if="item.policy_url&&item.policy_url!==null">电子保单</div>
              <div class="btn againPay" v-if="[5,6,7].filter(i=>i===item.status).length" @click='handleRePay(item)'>再次购买</div>
              <div class="btn toSettle" v-if="[4].filter(i=>i===item.status).length || ([5].filter(i=>i===item.status).length && dateDiff(item.policy_expiration_date))">申请理赔</div>
              <div class="btn toPay" v-if="item.status===0" @click='handleToPay(item)'>立即支付</div>
              <div class="btn rePay" v-if="[8].filter(i=>i===item.status).length" @click='handleToPay(item)'>重新支付</div>
              <div class="btn toDetail" v-if="[0,1,2,3,4,5,6,7,8].filter(i=>i===item.status).length" @click="goDetail(item)">查看详情</div>
            </div>
          </div>
          <div class="msg">
            <div class="ttl" v-text="item.name"></div>
            <div class="dl" v-if="item.relation_info.name && [1,2,3,4,5].filter(i=>i===item.status).length">保单号: {{item.policy_num}}</div>
            <div class="dl">被保险人：{{item.relation_info.name}}</div>
            <div class="dl"  v-if="item.relation_info.name && [0,7,6,8].filter(i=>i===item.status).length">创建时间：{{item.create_time}}</div>
            <div class="dl" v-if="item.relation_info.name && [1,2,3,4,5].filter(i=>i===item.status).length">保障期间: {{item.policy_effective_date}}至{{item.policy_expiration_date}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    message: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      baseUrl: `${process.env.VUE_APP_RESOURCE_URL}`
    }
  },
  mounted () {
    window.diff = this.dateDiff
  },
  methods: {
    goDetail (item) {
      this.$router.push({
        path: `/37duc/personCenter/orderDetail/${item.id}`,
        query: {
          orderId: item.id
        }
      })
    },
    handleRePay (item) { // 再次购买
      this.$router.push({
        query: {
          productId: item.product_id
        },
        path: `/37duc/product/detail/${item.product_id}`
      })
    },
    handleToPay (item) { // 重新支付 //立即支付
      this.$router.push({
        query: {
          sn: item.sn,
          productId: item.id
        },
        path: `/37duc/product/orderConfirm`
      })
    },
    dateDiff (odate) {
      if (!odate) return false
      var dateStr = new Date().getFullYear() + '-' + (new Date().getMonth() + 1) + '-' + new Date().getDate()
      var odate = odate.split(' ')[0]
      let  Date1  =  new  Date(dateStr)

      let Date2  =  new  Date(odate)
      let  iDays  =  parseInt(Math.abs(Date1  -  Date2)  /  1000  /  60  /  60  / 24)    // 把相差的毫秒数转换为天数
      console.log(iDays)
      return  iDays <= 200
    },
    status (status) {
      let res
      switch (status) {
        case 0:
          res = '待支付'
          break
        case 1:
          res = '已支付'
          break
        case 2:
          res = '已承保'
          break
        case 3:
          res = '已出单'
          break
        case 4:
          res = '保障中'
          break
        case 5:
          res = '已失效'
          break
        case 6:
          res = '承保失败'
          break
        case 7:
          res = '已取消'
          break
        case 8:
          res = '支付失败'
          break
      }
      return res
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/styles/mixin.scss';
  .orderBox {
    .orderList {
      .orderItem {
        background-color: #FFFFFF;
        border-bottom: 1px solid #B6BBBB;
				&.last-item{
					border-bottom: 0px;
				}
        .orderSn {
          padding: 15px 20px 15px 0;
          margin-left: 20px;
          border-bottom: 1px solid #F1F4F4;
          .sn {
            font-size: 14px;
            color: #B6BBBB;
          }
          .status {
            font-size: 16px;
          }
          .sn, .status {
            line-height: 18px;
            height: 18px;
          }
          .status0, .status1,  .status2,.status3, .status5 ,.status6,.status7 ,.status8{
            color: #B6BBBB;
          }
          /*.status3 {*/
            /*color: #B6BBBB;*/
          /*}*/
          .status4 {
            color: #1FD8D2;
          }
        }
        .orderMsg {
          .btn-price {
            position: relative;
            padding: 20px;
            padding-left: 0;
            .logo {
              height: 28px;
              margin-bottom: 20px;
              text-align: right;
              img {
                height: 100%;
              }
            }
            .price {
              margin: 20px 0;
              text-align: right;
              .p {
                font-size: 20px;
                line-height: 22px;
                color: $color-active-text;
              }
              .i {
                line-height: 18px;
                font-size: 12px;
                color: #D5D8D8;
              }
              .p, .i {
                vertical-align: bottom;
              }
            }
            .btns {
              margin-top: 20px;
              margin-right: -16px;
              white-space: nowrap;
              text-align: right;
              .btn {
                display: inline-block;
                margin-right: 16px;
                line-height: 34px;
                font-size: 14px;
                width: 87px;
                border-radius: 18px;
                letter-spacing: 1px;
                text-align: center;
                cursor: pointer;
                &.toDetail {
                  color: #FFFFFF;
                  background: #1FD8D2;
                  border: 1px solid $color-active-text;
                }
                &.toPay, &.againPay, &.rePay {
                  color: #FF4B4B;
                  background: #FFFFFF;
                  border: 1px solid #FF4B4B;
                }
                &.baodan {
                  color: #A5A8A8;
                  border: 1px solid #D5D8D8;
                  background: #FFFFFF;
                }
                &.toSettle {
                  color: #1FD8D2;
                  border: 1px solid #1FD8D2;
                  background: #FFFFFF;
                }
              }
            }
          }
          .msg {
            padding: 12px 20px;
            overflow: hidden;
            .ttl {
              line-height: 36px;
              font-size: 20px;
              color: $color-text;
              font-weight: 600;
              text-align: left;
              margin-bottom: 10px;
            }
            .dl {
              line-height: 30px;
              font-size: 14px;
              color: $color-text;
              text-align: left;
            }
          }
        }
      }
    }
  }
</style>
